import Navbar from '../component/navbar.jsx';
//import MapContainer from './mapContainer.jsx';
import ToTop from '../component/toTop.jsx';

export default class Map extends React.Component {
  constructor(props) {
    super(props);

    this.state = {}

  }
  render() {
    var content = (
      <div>
        <Navbar/>
        <h1 className='container'>Placeholder for Map component</h1>
        {/* <Footer/> */}
        <div className="row">
          <div className="col-md-12 col-sm-8">
            {/* <MapContainer {...this.props.mapProps}/> */}
          </div>
        </div>
        <ToTop/>
      </div>
    );

    return content;
  }

};



